.input{
    display: flex;
    width: 90%;
    position: relative;
    align-items: center;
}

.input_box{
    width: 100%;
    border-radius: 40px;
    padding: 20px 30px;
    font-size: 25px;
    border: none;
    transition: 0.2s;
    box-shadow: inset 0 0 5px #403c37;
}

.input_box:focus{
    box-shadow: 0 0 10px 1000px #403c37;
    outline: none;
}

.input_submit{
    position: absolute;
    width: 50px;
    height: 50px;
    margin: 12px;
    border-radius: 50px;
    right: 0;
    border: none;
    font-size: 15px;
    background-color: #f5d295;
    color: #403c37;
    transition: 0.2s all;
    box-shadow: 0 0 10px #403c37;
}

.input_submit:hover{
    background-color: #f5d295;
}

.input_submit:active{
    transform: scale(0.8);
    box-shadow: 0 0 5px #403c37;
}